@use "variables";
@use "base";
@use "hero";
@use "timeline";
@use "cards";
@use "reco";
@use "contact";
@use "celebrate";
@use "button";

/* Le SVG fait la taille TOTALE du document et scrolle avec la page */
#bg-doc{
  position:absolute; /* suit le flux du document */
  top:0; left:0; width:100%;
  height:auto; /* sera regle en JS */
  pointer-events:none;
  z-index:0;          /* derriere tes cards */
  mix-blend-mode:screen;
   will-change: transform;
}
:root[data-theme="light"] #bg-doc{ mix-blend-mode:normal; }

/* Traits nets + glow */
#bg-snake, #bg-snake-glow{
  shape-rendering:geometricPrecision;
  vector-effect:non-scaling-stroke;
  stroke-linecap:round;
  stroke-linejoin:round;
}
#bg-snake-glow{ stroke-width:6; filter:url(#bg-glow-strong); opacity:.55; }
#bg-snake     { stroke-width:4;  filter:url(#bg-glow-soft); }

#bg-wrap{
  position: fixed;
  inset: 0;
  pointer-events: none;
  z-index: 0;
  overflow: hidden;
}
header, .hero, #content { position: relative; z-index: 1; }